import React from 'react';

import './collection.css'



function Collection() {
  return (
    <div className="collection">
      
      <div className="collection-title"><strong >Paso 2.</strong> Descarga tu coleccin  <span className="collection-title-span">(Esta es una vista previa)</span> </div>

      <div className="collection__view">
        <div className="view pink">SU VISTA PREVIA SE MOSTRAR AQU</div>
        <div className="view green">SU VISTA PREVIA SE MOSTRAR AQU</div>
        <div className="view blue">SU VISTA PREVIA SE MOSTRAR AQU</div>
      </div>

      <div className="form">
        <div className="form-group">
          <label className="form-group--title">Tamao*</label>
          <input type="number" placeholder="10000"  className="form-group--input"/>

        </div>

        <div className="form-group">
          <label className="form-group--title">Nombre <span className="placeholder">(Opcional)</span></label>
          <input type="text" placeholder="Nombre de su coleccin"  className="form-group--input"/>

        </div>

        <div className="form-group">
          <label className="form-group--title">Descripcin <span className="placeholder">(Opcional)</span></label>
          <input type="text" placeholder="Descripcin de su coleccin"  className="form-group--input"/>

        </div>

      </div>


      <button className="collection__button">Descargar muestra gratuita</button>
      <button className="collection__button download">Descargar Coleccin</button>
        
      
    </div>
  );
}

export {Collection};